<template>
	<div id="income">
		<van-nav-bar fixed style="z-index:1001">
			<div slot="title">
				收入榜单
			</div>
			<van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
		</van-nav-bar>
		<div id="container" style="z-index:1000;margin-top:40px;">
			<van-row type="flex" justify="center" class="itme">
			  <van-col span="8">排名</van-col>
			  <van-col span="8">人气达人</van-col>
			  <van-col span="8">总收入</van-col>
			</van-row>
			<van-row type="flex" justify="center" class="itme" v-for="(item,index) in incomeLists" :key="index">
			  <van-col span="8">{{index + 1}}</van-col>
			  <van-col span="8">
			  	<img src="/static/img/good.png" alt="" class="portrait">
			  	<span class="username">{{item.nickname}}</span>
			  </van-col>
			  <van-col span="8" style="color: #ed3c2b;">{{item.credit5}}元</van-col>
			</van-row>
		</div>
	</div>
</template>

<script>
  export default {
    name: "income",
    data() {
      return{
        incomeLists:[],
      }
    },
    mounted(){
      this.incomeList();
    },
    methods:{
      incomeList() {
        let self = this;
        this.$POST('/user/list',true).then(res =>{
          console.log(res);
          if (res.code === 200) {
            self.incomeLists = res.data
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
    }
  }
</script>

<style scoped>
	#income{
		font-size: 1rem;
		font-family: PingFang-SC-Regular;
		color: #000000;
		background-color: #eeeeee;
	}

	#container .portrait{
		width: 2rem;
		height: 2rem;
		border-radius: 2rem;
		float: left;
		margin-left: 1rem;
		margin-top: -0.2rem;
	}
	#container .username{
		display: inline-block;
		margin-left: 0.6rem;
		float: left;
	}
	#container .itme{
		padding: 1rem;
		margin-top: 0.1rem;
		background-color: #ffffff;
	}
</style>



